@import '../../../MediaQueries/MediaQueries.scss';

@mixin center_flex {
	display: flex;
	justify-content: center;
	align-items: center;
}

.landing_container {
	height: 100vh;
	width: 100vw;
	background: #214054;
	@include center_flex();
	flex-direction: row;
	margin-bottom: 50px;
}

